    tipo: 'MENU',
    botoes: ['Download Regulamento', 'Aderir Plano', 'Começar outra vez'],
    "Download Regulamento|www": 'https://www.google.com.br/webhp?sourceid=chrome-instant&ion=1&espv=2&ie=UTF-8#q=falta+fazer+o+PDF+com+o+regulamento',
